import React from 'react';

import onPaste from './onPaste';
const Editor = ({ children, editor, hocs, readOnly }) => {
	return (
		<React.Fragment>
			{children}
			{hocs &&
				hocs.length &&
				hocs.map((hoc) => {
					return !readOnly && hoc(editor);
				})}
		</React.Fragment>
	);
};

//options.HOC = [(editor,readOnly)=>HighOrderComponent] ,HighOrderComponenteditorReact.component
export default (options = {}) => {
	if (Array.isArray(options.plugins)) {
		return {
			renderEditor: (props, editor, next) => {
				const children = next();
				return (
					<Editor editor={editor} readOnly={props.readOnly} hocs={options.plugins}>
						{children}
					</Editor>
				);
			},
			onPaste
		};
	} else
		return {
			renderEditor: (props, editor, next) => {
				const children = next();
				return <React.Fragment>{children}</React.Fragment>;
			},
			onPaste
		};
};
