import React from 'react';
import { cx, css } from 'emotion';
import { componentClassName } from '../theme';
// schema hr isVoidtruebackspace
const Hr = React.forwardRef((props, ref) => {
	const { isSelected = false, ...attributes } = props;
	return (
		<div
			ref={ref}
			{...attributes}
			className={cx(
				css`
					width: 100%;
					height: 2px;
					margin: 1rem 0;
					background: #eee;
					cursor: pointer;
					border-style: solid;
					border-width: 1px;
				`,
				componentClassName.Hr,
				css`
					border-color: ${isSelected ? 'var(--theme-color, #42b983)' : 'transparent'};
				`
			)}
		/>
	);
});

export default Hr;
