import React, { useEffect, useRef, useState } from 'react';
import Tooltip from 'rc-tooltip';
import 'rc-tooltip/assets/bootstrap.css';
import List from './components/List';
import { cx } from 'emotion';
const iconItems = [
	{ text: '', icon: 'icon-formatheader1', block: 'heading1', shortCut: '+1' },
	{ text: '', icon: 'icon-formatheader2', block: 'heading2', shortCut: '+2' },
	{ text: '', icon: 'icon-formatheader3', block: 'heading3', shortCut: '+3' },
	{ text: '', icon: 'icon-formatheader4', block: 'heading4', shortCut: '+4' },
	{ text: '', icon: 'icon-formatheader5', block: 'heading5', shortCut: '+5' },
	{ text: '', icon: 'icon-formatheader6', block: 'heading6', shortCut: '+6' },
	{ text: '', icon: 'icon-paragraph', block: 'paragraph', shortCut: '+p' },
	{ text: '', icon: 'icon-double-quotes-l', block: 'block-quote', shortCut: '+>' },
	{ text: '', icon: 'icon-editor-list-numbers', block: 'ordered', shortCut: '+shift+l' },
	{ text: '', icon: 'icon-editor-list-bulleted', block: 'bulleted', shortCut: '+l' },
	{ text: '', icon: 'icon--Todo-List', block: 'undo', shortCut: '+shift+o' },
	{ text: '', icon: 'icon-formula', block: 'math', shortCut: '' }
];
const WrapperBlock = ({ visible, wrapper, children }) => {
	useEffect(() => {
		console.log('init');
		return () => {
			console.log('clean');
		};
	}, []);
	return (
		<div style={{ position: 'relative' }}>
			{visible && wrapper}
			{children}
		</div>
	);
};

const DemoWrapper2 = ({ editor, node }) => {
	const [ visible, setVisible ] = useState(false);
	const triggerRef = useRef(null);
	const handleClick = (e) => {
		e.stopPropagation();
		e.preventDefault();
	};
	let iconItem = iconItems.find((i) => i.block === node.type);
	if (!iconItem) {
		switch (node.type) {
			case 'todo-list':
				iconItem = { icon: 'icon--Todo-List' };
				break;
			case 'ordered-list':
				iconItem = { icon: 'icon-editor-list-numbers' };
				break;
			case 'bulleted-list':
				iconItem = { icon: 'icon-editor-list-bulleted' };
				break;
			default:
		}
	}
	return (
		<div
			contentEditable={false}
			style={{
				position: 'absolute',
				left: '-50px',
				top: '-5px',
				fontSize: '16px',
				padding: '4px',
				cursor: 'pointer'
			}}
			onClick={handleClick}
			ref={triggerRef}
		>
			<Tooltip
				visible={visible}
				// animation="zoom"
				placement="rightTop"
				destroyTooltipOnHide={true}
				onVisibleChange={(v) => setVisible(v)}
				trigger="click"
				overlay={<List editor={editor} node={node} hiddenMenu={() => setVisible(false)} items={iconItems} />}
			>
				<span
					className={cx('iconfont', (iconItem && iconItem.icon) || '')}
					style={{ fontSize: '1.2rem', color: '#666' }}
				/>
			</Tooltip>
		</div>
	);
};

/**
 * optionsWrapperwrapper inject  {editor}props
 * demoWrapper
 */
export default (options = {}) => {
	const { Wrapper = DemoWrapper2 } = options;
	return {
		renderBlock: (props, editor, next) => {
			const children = next();
			const { node, isSelected } = props; // isFocused,
			const { isExpanded } = editor.value.selection; //
			const { document } = editor.value;
			const { type } = node;
			if (type !== 'list-item' && document.nodes.includes(node))
				return (
					<WrapperBlock visible={isSelected && !isExpanded} wrapper={<Wrapper node={node} editor={editor} />}>
						{children}
					</WrapperBlock>
				);
			return <React.Fragment>{children}</React.Fragment>;
		}
	};
};
